  .contacts__inner {
    display: flex;
    justify-content: space-between;
    width: 100%; }
  .contacts__item {
    content: "";
    width: 23%;
    height: 400px;
    border: 1px solid white;
    background-color: rgba(255, 255, 255, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
  .contacts__title {
    font-size: 22px;
    font-weight: 700;
    letter-spacing: 1.2;
    text-transform: uppercase;
    display: block; }
  .contacts__subtitle {
    font-size: 18px;
    font-weight: 600;
    margin-top: 30px;
    display: block; }
  .contacts a {
    color: white; }
    .contacts a:hover {
      text-decoration: underline; }
